function FloatIcons(){
let [whatsup,getMsg] = useState("")
const sendMsg = ()=>{
  let txt = ""; 
  const ele = document.querySelector('.v-chat-input').value;
  const newele = ele.split(" ")
  newele.forEach(el =>{
    txt += `${el}%20`
  })
  console.log(newele,txt);
}
const toggleChat =()=>{
  document.querySelector('.bubble').classList.toggle("hide")
}
// window.addEventListener('scroll',()=>{
//   const float = document.querySelector('.v-float-icons')
//   if (window.scrollY > 400){
//     float.style.display = 'block'
//   }else{
//     float.style.display = 'none'
//   }

    
// })

function gethome(){
  window.scrollTo(0,0);
}

  return(
    <div className="v-float-icons" >
    <img src={chat}  onClick={toggleChat} className="v-chat"  />
    <div>
    <div className="bubble">
    <i className="fa fa-close v-chat-close" onClick={toggleChat}></i>
      <div className="v-bubble-chat-wrapper">
        <img className="v-chat-dm" src={dm}/><div className="v-bubble-chat-text"><p>Hi, I am Vim. How may I help you?</p></div>
        
      </div>
      <div className="v-chat-input-wrapper">
      <input onChange={e=>getMsg(e.target.value)} className="v-chat-input" value={whatsup} />
      <button onClick={sendMsg} className="v-welcome-btn">Send</button>
      </div>
      
    </div>
    <img className="v-gohome" onClick={gethome} src={gohome} />
    </div>
    
   </div>
  )
}


export default function App() {

let [currPage,loadPage] = useState(<HomePage />)

const pageController = (e)=>{
window.scrollTo(0,0);
let t;
console.log(e.target.innerText);
  if (e.target.innerText){
t = e.target.innerText
}else{t = e}
  document.querySelector('.v-mobile-menu').style.transform = "translateX(-110%)"
  //console.log(t);
  switch (t) {
    case "Home":
    return   currPage = <HomePage />
    case "Web Design & Development":
      return  currPage = <WebDev />   
    case "App Design & Development":
      console.log('found');
      return  currPage = <AppDev />
    case "IT Support":
      return  currPage = <ItSupport />       
    case "Graphic Design":
      return  currPage = <GraphicDesign /> 
      case "Android Apps":
        return  currPage = <AndroidApps />     
        case "IOS Apps":
          return  currPage = <IosApps /> 
          case "Mac Os Apps":
      return  currPage = <MacApps />
      case "Windows Apps":
      return  currPage = <WinApps /> 
    case "About":
      return  currPage = <About /> 
    
    case "Contact":
      return  currPage = <Contact />
      case "Web Projects":
        return  currPage = <WebPros />
        case "Other Projects":
      return  currPage = <OtherPro />
    default:
      return   currPage;    
  }

  
}

  return (
    <div className="App v-themeable v-light-theme">
    <Header navBtnClick={(e)=>{loadPage(pageController(e))
    }}/>
    
    <div style={{
      paddingTop:"52px",minHeight:"400px"
    }}>{currPage}</div>
    <FloatIcons />
    <Footer />
    <Quote />
    </div>
  );

}

