import React, { useState } from 'react';
import ".././homepage.css"
import WelcomeScreen from ".././welcome"
import des from "../../assets/webdev1.png"
import ill from "../../assets/webill.svg"

export default function WebDev(){
  return(
    <div >
    
   <WelcomeScreen icon={des} title="Web Design & Development" text="Web Design and Web Devlopment has its own hold as a seperate modern means of communication. Build for yourself and business a modern website to reach out to the world in a matter of a single click or touch." />
   <div className="page-center"><h2 className="v-title-text1">Web Design</h2><p>Web design refers to the design of websites that are displayed on the internet. It usually refers to the user interface (UI) or visual part of website development rather than software development. Basically the "what you see" part like this very website is what the web designer makes possible. Without the design a website will be nothing but mere text with no awesome looks just like a word document.</p><p>The use of CSS, HTML and Javascript is basic way of building the web as a web designer. With Graphic design as another experience, web designers can take website to another level by designing custom icons, vector images and correct color matching to make the website more elegant.</p><p>Frameworks like Bootstap and materialize helps speed up the build process but not a necessity expecially for special websites that demands exclusive appearance for a reason.</p></div>

   <div className="page-center"><h2 className="v-title-text1">Web Development</h2><p>Web Developers on the other hand, take a website design and actually make a functioning website from it. Web developers use HTML, CSS, Javascript, PHP and other programming languages to bring to life the design files.</p><p>For example,a button switch on the top right (if you are on desktop or tablet) or in the menu labeled dark mode is a switch that makes the all white appearance turn dark. It works because of the logic behind the button just like a bulb and a switch. The combination of the web design and web development to make a website look alive, fully functional, ease of access and interesting for a user is called user experience (UX)</p></div>
   <img src={ill} style={{alignSelf: "center",
    display: "block",
    margin: "20px auto",
    width: "50%"}} />
    </div>
  )
}